				"html": "<h1>Java do..while Loop</h1>\n<p>The <code>do..while</code> loop is a special case of the <code>while</code> loop wherein the group of statements is guranteed to run atleast once before checking for a given condition. Confused? Ok, the follwing example should clear things up.</p>\n<p>Can you guess the output of the following code snippet?</p>\n<pre><code>int i=10;\ndo{\n    System.out.println(\"The value of i is \" + i);\n    i--;\n}while( i >= 10 );\n</code></pre>\n<p><code>Remember</code> : The condition of a <code>do-while</code> loop is checked After the code body is executed once.</p>\n<p>Now that you have some idea of basic syntax, let's delve into some of the most relevant and commonly used data types which will help you to develop programs in Java.Ja</p>",
